    saveSqlScript (sqlscript) {
      const h = this.$createElement
      let name = ''
      this.$msgbox({
        title: 'Save inquiry',
        message: h('div', null, [
          h('input', {
            domProps: {
              type: 'text',
              value: name,
              placeholder: '请输入名称'
            },
            class: {
              'el-input__inner': true
            },
            on: {
              change: function (event) {
                name = event.target.value // 监听change事件，手动的将值给保存到desc中
              }
            }
          })
        ]),
        showCancelButton: true,
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        beforeClose: (action, instance, done) => {
          if (action === 'confirm') {
            instance.confirmButtonLoading = true
            instance.confirmButtonText = '保存中...'
            console.log(name, 'name')
            setTimeout(() => {
              this.saveScript(name, sqlscript)
              done()
              setTimeout(() => {
                instance.confirmButtonLoading = false
              }, 30)
            }, 400)
          } else {
            done()
          }
        }
      }).then((action) => {
