    // 下载查询结果
    DownloadQuery: function () {
      this.$refs.form.validate((valid) => {
        if (valid) {
          if (this.export_json) {
            this.exportJsonLoading = true
          } else {
            this.exportCsvLoading = true
          }

          let self = this

          let param = JSON.stringify({
            model_name: this.$route.params.model_name,
            version_id: self.$route.params.version_id,
            sqlScript: this.form.sqlScript,
            table_name: this.tableName,
            exportJson: this.export_json,
            exportCsv: this.export_csv,
            page: self.moni_page
          })
          console.log(param, 'sqlScript')
          $.ajax({
            type: 'POST',
            url: test + '/api/sql/sql_script_excute',
            async: true,
            data: param,
            responseType: 'blob',
            headers: {
              'Content-Type': 'application/json',
              Authorization:
                'Token ' + JSON.parse(sessionStorage.getItem('token'))
            },
            timeout: 5000,
            success: (data) => {
              if (this.export_json) {
                this.exportJsonLoading = false
              } else {
                this.exportCsvLoading = false
              }

              // console.log(data,typeof data,"ddrun")

              if (this.export_json) {
                var blob = new Blob([data], { type: 'application/json;' })
                var link = document.createElement('a')
                var url = URL.createObjectURL(blob)
                link.setAttribute('href', url)
                link.setAttribute('download', 'export_query.json')
                link.style.visibility = 'hidden'
                document.body.appendChild(link)
                link.click()
                document.body.removeChild(link)
                // console.log(typeof data,data, "downloaddddd")
              } else {
                let blob = new Blob([data], {
                  type: 'text/csv;charset=utf-8;'
                })
                let link = document.createElement('a')
                let url = URL.createObjectURL(blob)
                link.setAttribute('href', url)
                link.setAttribute('download', 'export_query.csv')
                link.style.visibility = 'hidden'
                document.body.appendChild(link)
                link.click()
                document.body.removeChild(link)
              }
            }
          })
        }
      })
    },
    getTableStructure () {
      let self = this
