    open (tagstyle, cmd, cmdSpace) {
      const h = this.$createElement
      this.$msgbox({
        title: 'Stage Transition',
        message: h('div', null, [
          h('span', null, 'Transition to '),
          h('i', { style: 'color: teal', class: 'el-icon-right' }),
          h('strong', null, ' '),
          // h('span', { style: tagstyle, size: 'medium' }, cmdSpace)
          h('span', { class: tagstyle }, [h('strong', null, cmdSpace)])
        ]),
        showCancelButton: true,
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        beforeClose: (action, instance, done) => {
          if (action === 'confirm') {
            instance.confirmButtonLoading = true
            instance.confirmButtonText = '转换中...'
            setTimeout(() => {
              done()
              this.transformModelStagef(cmd)
              setTimeout(() => {
                instance.confirmButtonLoading = false
              }, 30)
            }, 300)
          } else {
            done()
          }
        }
      }).then((action) => {
        this.$message({
          type: 'success',
          message: '模型状态转换成功'
        })
      })
