        version_id: this.$route.params.version_id,
        path: curpath,
        filename: filename,
        p_type: 'cat',
        is_subdirectory: this.is_subdirectory,
        user_name: JSON.parse(sessionStorage.getItem('name'))
      }
      // let headers = {Authorization: 'Token '+JSON.parse(sessionStorage.getItem('token'))};
      let headers = {
        'Content-Type': 'application/json',
        Authorization: 'Token ' + JSON.parse(sessionStorage.getItem('token'))
      }
      catFileContent(headers, params).then((res) => {
        let { msg, code, data } = res
        if (code === '999999') {
          self.total = data.total
          self.is_subdirectory = data.is_subdirectory
          self.directory = data.directory
          self.back = data.back
          self._code = data.code
