        data: {
          model_name: this.$route.params.model_name,
          page: Number(self.page),
          is_dir: self.is_dir,
          version_id: this.$route.params.version_id,
          path: self.cur_path,
          p_type: self.p_type
        },
        headers: {
          Authorization: 'Token ' + JSON.parse(sessionStorage.getItem('token'))
        },
        timeout: 5000,
        success: function (data) {
          self.listLoading = false
          if (data.code === '999999') {
            self.total = data.data.total
            self.is_subdirectory = data.data.is_subdirectory
            self.directory = data.data.directory
            self.back = data.data.back
            self._code = data.data.code
            self.files = data.data.files
            // use to upload data
            self.pathParam = { pathParam: self.directory }
            if (self.is_subdirectory) {
              self.back_display = 'float:right'
            } else {
              self.back_display = 'display:none'
            }
            console.log(data, 'data')
          } else {
            self.$message.error({
              message: 'data.msg',
              center: true
            })
          }
        }
      })
    },
    handleStageStyle (currentstage) {
      if (currentstage === 'Production') {
        this.curr_stage_style = 'success'
        this.stag_act1_style = 'warning'
        this.stag_act1 = 'Staging'
        this.stag_act2_style = 'info'
        this.stag_act2 = 'None'
        this.stag_act3_style = 'info'
        this.stag_act3 = 'Archived'
      } else if (currentstage === 'Staging') {
        this.curr_stage_style = 'warning'
        this.stag_act1_style = 'info'
        this.stag_act1 = 'Archived'
        this.stag_act2_style = 'info'
        this.stag_act2 = 'None'
        this.stag_act3_style = 'success'
        this.stag_act3 = 'Production'
      } else if (currentstage === 'None') {
        this.curr_stage_style = 'info'
        this.stag_act1_style = 'info'
        this.stag_act1 = 'Archived'
        this.stag_act2_style = 'warning'
        this.stag_act2 = 'Staging'
        this.stag_act3_style = 'success'
        this.stag_act3 = 'Production'
      } else {
        this.curr_stage_style = 'info'
        this.stag_act1_style = 'info'
        this.stag_act1 = 'None'
        this.stag_act2_style = 'warning'
        this.stag_act2 = 'Staging'
        this.stag_act3_style = 'success'
        this.stag_act3 = 'Production'
      }
    },
    handleCommand (command) {
      // this.$message('click on item ' + command)
      console.log(command, 'command')
      // this.transferX = '<i class="el-icon-right" style="margin-right: 5px;margin-left: 5px;"></i><el-tag type="success"><strong>' + command + '</strong></el-tag>'
      if (command === 'Production') {
        // this.transferX = 'color: green'
        this.transferX = 'el-tag el-tag--success el-tag--small el-tag--light'
      } else if (command === 'Staging') {
        // this.transferX = 'color: #FFA500'
        this.transferX = 'el-tag el-tag--warning el-tag--small el-tag--light'
      } else {
        // this.transferX = 'color: #696969'
        this.transferX = 'el-tag el-tag--info el-tag--small el-tag--light'
      }
      let cmdSpace = command
      this.open(this.transferX, command, cmdSpace)
      // this.handleStageStyle(command)
    },
    transformModelStagef: function (stage) {
      let self = this
      // NProgress.start();
      let params = JSON.stringify({
        model_name: this.$route.params.model_name,
        stage: stage,
        version: this.$route.params.version_id,
        user_name: JSON.parse(sessionStorage.getItem('name'))
      })
      console.log(params, 'params')
      let header = {
        'Content-Type': 'application/json',
        Authorization: 'Token ' + JSON.parse(sessionStorage.getItem('token'))
      }
      transformModelStage(header, params).then((_data) => {
        let { msg, code } = _data
        if (code === '999999') {
          /* self.$message({
            message: '模型状态转换成功',
            center: true,
            type: 'success'
          }) */
          this.getModelsVersionMetaData()
        } else if (code === '999997') {
          self.$message.error({
            message: msg,
            center: true
          })
        } else {
          self.$message.error({
            message: msg,
            center: true
          })
          this.getModelsVersionMetaData()
        }
      })
    },
    // 获取模型元数据信息
    getModelsVersionMetaData () {
      let self = this
      let params = {
        user_key: JSON.parse(sessionStorage.getItem('name')),
        name: this.$route.params.model_name,
        version: this.$route.params.version_id
      }
      // let headers = {Authorization: 'Token '+JSON.parse(sessionStorage.getItem('token'))};
      let headers = {
        'Content-Type': 'application/json',
        Authorization: 'Token ' + JSON.parse(sessionStorage.getItem('token'))
      }
      getModelVersionBaseinfo(headers, params).then((res) => {
        // self.listLoading = false
        let { msg, code, data } = res
        if (code === '999999') {
          self.regedModelVersion = data
          this.handleStageStyle(data.current_stage)
          // self.cur_path = data.source
          if (this.regedModelVersion.description === '') {
            this.regedModelVersion.description = '无'
          }
          console.log(self.regedModelVersion, 'self.regedModelVersion')
        } else {
          self.$message.error({
            message: msg,
            center: true
          })
        }
      })
    },
    // 新增
    submitSave: function () {
      let self = this
      // NProgress.start();
      let params = JSON.stringify({
        model_name: self.$route.params.model_name,
        version: self.$route.params.version_id,
        description: self.editModelVersionMeta.description,
        user_name: JSON.parse(sessionStorage.getItem('name'))
      })
      console.log(params, 'params')
      let header = {
        'Content-Type': 'application/json',
        Authorization: 'Token ' + JSON.parse(sessionStorage.getItem('token'))
      }
      EditModelVersionDesc(header, params).then((_data) => {
        let { msg, code } = _data
        if (code === '999999') {
          self.$message({
            message: '添加成功',
            center: true,
            type: 'success'
          })
          this.descEditable = false
          this.descvisalble = true
          this.editModelVersionMeta.description = ''
          self.getModelsVersionMetaData()
        } else if (code === '999997') {
          self.$message.error({
            message: msg,
            center: true
          })
        } else {
          self.$message.error({
            message: msg,
            center: true
          })
          this.editModelVersionMeta.description = ''
          this.descEditable = false
          this.descvisalble = true
          self.getModelsVersionMetaData()
        }
      })
    },
    handelCancel: function () {
      this.descEditable = false
      this.descvisalble = true
      this.editModelVersionMeta.description = ''
    },
    handleEdit: function () {
      this.descEditable = true
      this.descvisalble = false
      this.editModelVersionMeta.description =
        this.regedModelVersion.description
    },
    handleChange (val) {
      console.log(val)
    },
    handleCurrentChange (val) {
      this.page = val
      this.getFileDir()
    },
    handleCurrentChange2 (val) {
      this.page_pred = val
      this.getpredInfo(this.isall)
    }
  },
  created () {
    console.log(this.Xvalue1, 'Xval')
  },
  mounted () {
    this.getModelsVersionMetaData()
    this.getFileDir()
    this.getpredInfo('all')
    this.getMonitorInfo()
    this.getTableStructure()
    // this.transferJupyter()
    // this.project = this.$route.params.project_id
    // this.case = this.$route.params.case_id
    // if (this.$route.params.firstID) {
    //   this.firstID = this.$route.params.firstID
    // } else {
    //   this.firstID = 'all'
    //  }
    // this.firstID = this.$route.params.firstGroup
    // console.log(this.firstID, 'this.$route.params.project_id')
  }
}
</script>

